import React from "react";
import PropTypes from "prop-types";
import injectSheet from "react-jss";
import FacebookProvider, { Comments } from "react-facebook";
import ReactDisqusComments from "react-disqus-comments";

require("core-js/fn/array/find");

import config from "../../../content/meta/config";

const styles = theme => ({
  postComments: {
    margin: "3em 0 0",
    padding: "3em 0 0",
    borderTop: "1px solid #ddd"
  }
});

class PostComments extends React.Component {
  constructor(props) {
    super(props);
  }
  // }
  // } = props => {

  handleNewComment(e) {
    e.preventDefault();
    console.log(e.text);
  }
  render() {
    console.log(this.props);
    const { classes, slug, facebook } = this.props;
    return (
      <div id="post-comments" className={classes.postComments}>
        <ReactDisqusComments
          shortname="techgatsblog"
          identifier={this.props.post.fields.prefix}
          title={this.props.post.frontmatter.title}
          url={`${config.siteUrl}${slug}`}
          category_id={this.props.post.fields.prefix}
          onNewComment={this.handleNewComment}
        />
        <FacebookProvider appId={facebook}>
          <Comments
            href={`${config.siteUrl}${slug}`}
            width="100%"
            colorScheme={this.props.theme.main.colors.fbCommentsColorscheme}
          />
        </FacebookProvider>
      </div>
    );
  }
}

PostComments.propTypes = {
  classes: PropTypes.object.isRequired,
  post: PropTypes.object.isRequired,
  slug: PropTypes.string.isRequired,
  theme: PropTypes.object.isRequired,
  facebook: PropTypes.object.isRequired
};

export default injectSheet(styles)(PostComments);



